<template>
  <div class="tax-page param-add">
    <h4>区域设置<span>/</span>{{this.id?'修改':'添加'}}区域</h4>
    <div class="content">
      <el-form ref="inputForm" :model="area" :rules="rules" label-width="120px">
        <el-form-item label="区域名称" prop="name">
          <el-input v-model="area.name" placeholder="请输入区域名称"></el-input>
          <div class="form-tip">请填写区域名称.</div>
        </el-form-item>
        <el-form-item label="区域描述" prop="remarks">
          <el-input v-model="area.remarks" type="textarea" :rows="4" placeholder="请输入区域描述"></el-input>
          <div class="form-tip">简单描述此区域.</div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
          <swak-link class="el-button" to="/system/area">返回</swak-link>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import SwakLink from "../../ui/components/link/link";
  import AreaApi from '@/api/system/area';

  export default {
    components: {
      SwakLink
    },
    name: "area-add",
    data() {
      return {
        area: {
          id: '',
          name: '',
          remarks: '',
        },
        rules: {
          name: [
            {required: true, message: '请输入区域名称', trigger: 'blur'},
          ],
          remarks: [
            {required: true, message: '请输入区域描述', trigger: 'blur'},
          ]
        }
      }
    },
    mounted() {
      this.$emit('layout:load', "area")
      var id = this.$route.params.id;
      this.load(id)
    },
    methods: {
      load: function (id) {
        if (!id) {
          return;
        }
        AreaApi.get(id).then(res => {
          if (res.obj) {
            this.area = res.obj;
          }
        });
      },
      onSubmit: function () {
        this.$refs['inputForm'].validate(res => {
          if (res) {
            AreaApi.save(this.area).then(data => {
              this.$notify.success("添加区域成功!");
              this.$router.replace("/system/area");
            });
          } else {
            this.$notify.error("表单错误，请输入必填项目");
          }
        })
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../ui/common/stylus/variable.styl"
</style>