import fetch from '../fetch'

/**
 * Area service
 * @type {{list: function()}}
 */
const Area = {
  get: (id) => fetch({
    url: '/api/manage/area/get/' + id,
    method: 'get'
  }),
  list: () => fetch({
    url: '/api/manage/area/areas',
    method: 'get'
  }),
  provinces: () => fetch({
    url: '/api/manage/area/provinces',
    method: 'get'
  }),
  citys: (id) => fetch({
    url: '/api/manage/area/citys?province=' + id,
    method: 'get'
  }),
  save: (area) => fetch({
    url: '/api/manage/area/save',
    method: 'post',
    data: area
  }),
  delete: (area) => fetch({
    url: '/api/manage/area/delete',
    method: 'post',
    data: area
  }),
}

export default Area